





































import Vue from 'vue';
import { Menu, MenuItemGroup, MenuItem } from 'element-ui';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import VueShortkey from 'vue-shortkey';
import { menuItems } from './cassandra-nav-menu-items';

Vue.use(VueShortkey);

export default Vue.extend({
  name: 'CassQueryNavMenu',
  components: {
    [Menu.name]: Menu,
    [MenuItemGroup.name]: MenuItemGroup,
    [MenuItem.name]: MenuItem,
    FontAwesomeIcon,
  },
  props: {
    clusterName: {
      type: String,
      required: true,
    },
    value: {
      type: String,
      validator(value: string) {
        return (
          value === undefined || menuItems.some((item) => item.name === value)
        );
      },
    },
  },
  data() {
    return {
      menuItems,
    };
  },
  computed: {
    isExpanded(): boolean {
      return !!this.value;
    },
  },
  methods: {
    onSelectMenu(view) {
      this.$emit('selected', view == this.value ? undefined : view);
    },
  },
});
